<template>
  <div class="cart bg-primary">
    <div class="search-frame fixed-top px-2 py-4 searchgbar bg-secondary d-flex justify-content-between">
      <img src="../assets/images/profileIcon.png" alt="" height="30px">
      <p class="mb-0 h5 text-white d-flex align-items-center">我的購物車</p>
      <img src="../assets/images/backhome.png" alt="" height="30px" @click="$router.push({ name: 'Store' })">
    </div>
    <div class="cart-list">
      <div class="p-2">
        <div v-for="(item,index) in productList" :key="index" class="d-flex p-1 bg-white mb-2">
          <img :src="item.img" alt="" class="w-25 h-25 img-fluid mr-2" height="">
          <div class="w-50 position-relative">
            <p class="mb-0 text-left">{{ item.name }}</p>
            <div class="numbercal d-flex border border-black w-50 position-absolute mb-0 h-25 justify-content-around">
              <p @click="handleSub(item)">-</p>
              <p>{{ item.count }}</p>
              <p @click="handlePlus(item)">+</p>
            </div>
          </div>
          <p v-if="item.discount" class="d-flex align-items-center  text-truncate justify-content-center mb-0 w-25 text-right text-danger float-right"> ${{ item.discount | currency }} </p>
          <p v-else class="d-flex align-items-center  text-truncate justify-content-center mb-0 w-25 text-right text-danger float-right"> ${{ item.price | currency }} </p>
          <div class="bg-danger text-white d-flex align-items-center" @click="del(index)">移除</div>
        </div>
        <div v-if="!productList || productList.length === 0">
          <p>您的購物車目前是空的</p>
          <u>
            <router-link class="text-secondary" to="/store">去逛逛吧</router-link>
            <router-view/>
          </u>
        </div>
      </div>
    </div>
    <div class="fixed-bottom shadow-lg bg-secondary d-flex justify-content-between">
      <p class="pl-2 text-white mb-0 h5 text-left align-items-center d-flex">總金額: $ {{ totalMoney }}</p>
      <div class="bg-white py-3 px-6 h5 mb-0 shadow-lg">結帳</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Cart',
  data () {
    return {
      productName: '',
      productList: []
    }
  },
  created () {
    // console.log(localStorage.getItem('productCart'))
    this.productList = JSON.parse(localStorage.getItem('productCart'))
  },
  filters: {
    currency (price) {
      return price.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    }
  },
  computed: {
    totalMoney () {
      if (!this.productList) return 0
      const money = this.productList.map(item => {
        return item.count * (item.discount ? item.discount : item.price)
      })
      const total = money.reduce((val, cur) => {
        return val + cur
      }, 0)
      return total
      // return this.productList.map(item => {
      //   return item.count * (item.discount ? item.discount : item.price)
      // }).reduce((val, cur) => {
      //   return val + cur
      // })
    }
  },
  methods: {
    handleSub (item) {
      if (item.count === 1) return
      item.count--
      localStorage.setItem('productCart', JSON.stringify(this.productList))
    },
    handlePlus (item) {
      item.count++
      localStorage.setItem('productCart', JSON.stringify(this.productList))
    },
    del (index) {
      this.productList.splice(index, 1)
      // console.log(this.productList.length)
      // localStorage.setItem('productCart', JSON.stringify(this.productList))
      // console.log(this.productList)
      if (this.productList.length <= 0) {
        console.log(this.productList)
        localStorage.removeItem('productCart')
        this.productList = []
      } else {
        localStorage.setItem('productCart', JSON.stringify(this.productList))
      }
    }
  }
}
</script>
